/* https://www.digitalocean.com/community/tutorials/css-minimal-css-reset */
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap");
html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*GENERAL*/
.app-container {
  background-color: rgb(255, 255, 255);
}

.page-container {
  margin: 4em;
}
.page-container#home {
  text-align: left;
}

section {
  padding-left: 15%;
  padding-right: 15%;
}

header {
  background-image: url(../images/student-laptop-books.jpg);
  background-position: center;
  background-size: cover;
  padding-top: 5%;
  padding-bottom: 5%;
  padding-left: 25%;
  padding-right: 25%;
}

.text-box {
  background-color: gainsboro;
  border-radius: 10px;
  border: solid 2px white;
  padding: 4%;
}

/*FOOTER*/
footer {
  background-color: #63B891;
  padding: 1%;
  padding-left: 2%;
  margin-top: 1%;
}

html {
  font-size: 100%;
}

/*16px*/
body {
  background: white;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  line-height: 1.75;
  color: #000000;
}

header {
  text-align: center;
}

p {
  margin-bottom: 1rem;
}

h1,
h2,
h4,
h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
  text-align: center;
  padding-top: 1%;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

/*NAV BAR */
nav {
  display: flex;
  padding: 2% 6%;
  justify-content: space-between;
  align-items: center;
}

nav img {
  width: 40%;
}

.nav-links {
  flex: 1;
  text-align: right;
}

.nav-links li {
  list-style: none;
  display: inline;
  padding: 8px 12px;
  position: relative;
}

.nav-links ul li a {
  color: black;
  font-size: 16px;
  text-decoration: none;
}

nav .highlight, nav .current a {
  font-weight: bold;
}

a:hover {
  font-weight: bold;
}

nav .fa {
  display: none;
}

#task-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "taskform tasklist";
}

#taskform {
  grid-area: taskform;
  max-width: 300px;
  margin: 0 auto;
}
#taskform label {
  display: block;
  width: 100%;
}
#taskform input {
  width: 100px;
}

#tasklist {
  grid-area: tasklist;
}

button {
  background-color: gainsboro;
  color: white;
  border: 1px solid white;
  display: inline-block;
  width: 150px;
  padding-top: 0.5%;
  padding-bottom: 0.5%;
  border-radius: 6px;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  transition: ease-out 0.2s;
}

button:hover {
  box-shadow: inset 200px 0 0 0 #98D6F0;
}

